.movies {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    margin: 20px;
    margin-top: 150px;
    min-height: 100vh;
}

.posters {
    text-align: center;
    margin: 20px;
    width: 238px;
    position: relative;
}

.movie-title {
    margin: 10px 0;
}

.movie-img {
    position: relative;
    opacity: 1;
    transition: .5s ease;
    backface-visibility: hidden;
}

.card-img-overlay {
    width: 238px;
    height: 340px;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.btn {
    width: 80%;
    height: 50px;
    margin: 10px;
    font-size: 20px;
    color: rgb(216, 216, 216);
    border-color: rgb(216, 216, 216);
    /* background-color: #515151; */
    padding: 0 20px;
}

.btn:visited {
    color: white;
    border-color: white;
}

.btn:hover {
    color: white;
    border-color: white;
}

.btn:active {
    color: white;
    border-color: white;
}

.posters-container:hover .movie-img {
    opacity: 0.3;
}

.posters-container:hover .card-img-overlay {
    opacity: 1;
}

html {
    --antd-wave-shadow-color: white;
}